        "content":"\"Приношу извинения перед Вами, что Я совершил преступление, предусмотренное ст. 228 УК РФ\".\n\nЧтобы Абаканский суд закрыл уголовное дело, мужчина дважды раскаялся на страницах местной газеты — за покупку травки. \n\nИлгар Шукуров — обычный работяга: работа, семья, дети. Вырваться из рутины и расслабиться хотел в компании 2,5 грамма гашиша. Но попался — завели уголовку по 228-й, это до трёх лет. Мужчина взял всё в свои руки.\n\nОн стал писать в газету \"Шанс\" исповеди и просить прощения у жителей. Параллельно помогал деньгами детскому дому и как мог показывал, что он на пути исправления. Суд поверил — дело закрыли, наказанием стал штраф в 15к. \n\nпризнал свои косяки",
